import React from 'react';
import PropTypes from 'prop-types';
import { isFunction, mapValues } from 'lodash';
import { isAttrTrue } from '@devsnc/sn-controls-common';
import style from './style.scss';
import '@servicenow/now-tooltip';
import '@servicenow/now-button';

const oldIconToEDSIcon = {
	search: 'magnifying-glass-outline',
	phone: 'phone-outline',
	'info-circle': 'info-circle-outline'
};

const getIcon = old => oldIconToEDSIcon[old] || old;

const handleButtonClick = ({ state: { properties }, action }) => {
	let { onClick } = properties;

	if (isFunction(onClick)) onClick(action);
};

const actionHandlers = {
	'NOW_BUTTON#CLICKED': { effect: handleButtonClick, stopPropagation: true },
	'NOW_BUTTON_BARE#CLICKED': {
		effect: handleButtonClick,
		stopPropagation: true
	},
	'NOW_BUTTON_ICONIC#CLICKED': {
		effect: handleButtonClick,
		stopPropagation: true
	}
};

export default class Control extends React.PureComponent {
	static style = style;

	static actionHandlers = actionHandlers;

	static propTypes = {
		/**
		 * Text for the button
		 */
		label: PropTypes.string,

		/**
		 * Variant for the button
		 */
		variant: PropTypes.string,

		/**
		 * Size of the button
		 */
		size: PropTypes.string,

		/**
		 * Size of the button
		 */
		reverseIconPlacement: PropTypes.bool,

		/**
		 * Size of the button
		 */
		bare: PropTypes.bool,

		/**
		 * Label for the tooltip
		 */
		tooltip: PropTypes.string,

		/**
		 * Icon for the control
		 */
		icon: PropTypes.string,

		/**
		 * Whether button is iconic
		 */
		iconic: PropTypes.bool,

		/**
		 * ref callback to inner button element
		 */
		innerRef: PropTypes.func,

		/**
		 * Click handler for the control
		 */
		onClick: PropTypes.func,

		/**
		 * Is the control disabled
		 */
		disabled: PropTypes.bool,

		/**
		 * aria-* attribute configuration
		 */
		configAria: PropTypes.object,

		/**
		 * Content to render within the control button
		 */
		children: PropTypes.node
	};

	showTooltip = () => this.setState({ showTooltip: true });
	hideTooltip = () => this.setState({ showTooltip: false });

	handleButtonRef = node => {
		this.button = node;

		this.button.addEventListener('focus', this.showTooltip);
		this.button.addEventListener('blur', this.hideTooltip);
		this.button.addEventListener('mouseover', this.showTooltip);
		this.button.addEventListener('mouseout', this.hideTooltip);

		if (isFunction(this.props.innerRef)) {
			this.props.innerRef(node);
		}
	};

	state = {
		showTooltip: false
	};

	render() {
		let icon = getIcon(this.props.icon);

		let {
			disabled = false,
			variant = 'tertiary',
			size = 'sm',
			bare = true,
			iconic = false,
			configAria = {},
			reverseIconPlacement,
			label,
			tooltip
		} = this.props;
		let ButtonComponent = 'now-button';
		if (bare && iconic) {
			ButtonComponent += '-iconic';
		}

		let children = reverseIconPlacement ? (
			<React.Fragment>
				{label && <span className="now-line-height-crop">{label}</span>}

				{!iconic && icon && (
					<now-icon
						className="now-button-icon"
						icon={icon}
						size={size}
						aria-hidden="true"
					/>
				)}
			</React.Fragment>
		) : (
			undefined
		);

		return (
			<React.Fragment>
				<ButtonComponent
					ref={this.handleButtonRef}
					label={label}
					size={size}
					icon={icon}
					icon-name={icon}
					icon-start={icon}
					disabled={isAttrTrue(disabled) ? true : undefined}
					variant={variant}
					bare={bare}
					config-aria={{
						// handles when tooltip is undefined
						'aria-label': tooltip || '',
						// allow for explicit overrides of aria-label
						...configAria
					}}
				>
					{children}
				</ButtonComponent>
				{this.state.showTooltip && tooltip && (
					<now-tooltip
						target-ref={this.button}
						content={tooltip}
						manage-opened
						opened
					/>
				)}
			</React.Fragment>
		);
	}
}
